@tailwind base;
@tailwind components;
@tailwind utilities;

::selection {
    color: #8c52ff;
    background: #ff914d;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #8c52ff;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #ff914d;
    border-radius: 5px;
    transition: background 3ms ease;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #ff914d;
}

.scroll-container {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

.scroll-container::-webkit-scrollbar {
    display: none;  /* Chrome, Safari, Opera */
}
